(function(angular) {
  'use strict';

  angular.module('medszretailer')
    .service('otcService', otcService);

  /** @ngInject */
  function otcService($q, getData) {
    var service = {};
    // var url = 'assets/json/otc.json';
    var url = ['http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/product/otcproducts_list/', 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/pharmacy/pharmacyotcsub/'];

    service.getUserData = getUserData;
    service.postOtcData = postOtcData;

    function postOtcData(data) {
      // console.log(data);
      var deferred = $q.defer();
      var getURL = url[1];
      getData.relQuery(getURL, data)
        .then(function(response) {
          deferred.resolve(response);
          // if(){
          //   deferred.resolve(response);
          // }else {
          //   deferred.reject(response);
          // }
      });
      return deferred.promise;
    }
    
    function getUserData() {
      var deferred = $q.defer();
      var getURL = url[0];
      getData.async(getURL)
        .then(function(response) {
          deferred.resolve(response);
          // if(){
          //   deferred.resolve(response);
          // }else {
          //   deferred.reject(response);
          // }
      });
      return deferred.promise;
    }

    return service;
  }
})(angular);
(function(angular) {
  'use strict';

  angular
    .module('medszretailer')
    .controller('OtcController', OtcController);

  /** @ngInject */
  function OtcController($scope, $window, $state, $localStorage, $mdDialog, $mdToast, otcService) {
    var vm = this;
    var healthListener;
    var babyListener;
    var otherListener;

    vm.noHealth = false;
    vm.noBaby = false;
    vm.noOther = false;

    vm.healthCare = [];
    vm.babyCare = [];
    vm.others = [];
    vm.selectedHealthCare = [];
    vm.selectedBabyCare = [];
    vm.selectedOthers = [];

    vm.listItemUpdate = listItemUpdate;
    vm.selectedEdit = selectedEdit;
    vm.selectedDelete = selectedDelete;
    vm.saveToLocal = saveToLocal;
    

    otcService.getUserData()
        .then(function(res) {
          // console.log(res);
        	listedMakeOver(res.results);
    });

    function listedMakeOver(response){
      var list = response.map(function (temp, index) {
    	    var result = [];
          if(temp.category == '1'){
            result = vm.babyCare;
          } else if(temp.category == '2'){
            result = vm.healthCare;
          } else {
            result = vm.others;
          }

          result.push({
            "pharmacy": $localStorage['Pharmausr'].userid,
            "otcproduct": temp.id,
            // "instruction": temp.description,
            "name": temp.name,
            "img": temp.productimage,
            "mrp_price": "",
            "sale_price": "",
            "Action": false
          });
    	});
    }

    function selectedEdit(item) {
    	item.edit = false;
    	// console.log(item);
    }

    function selectedDelete(item, pagename) {
    	var selectedIndex;
    	var listedIndex;
    	if(pagename == 'healthcare') {
    		selectedIndex = hasId(item, vm.selectedHealthCare);
    		listedIndex = hasId(item, vm.healthCare);
    		vm.selectedHealthCare.splice(selectedIndex, 1);
    		vm.healthCare[listedIndex].Action = false;
    	}else if(pagename == 'others'){
    		selectedIndex = hasId(item, vm.selectedOthers);
    		listedIndex = hasId(item, vm.others);
    		vm.selectedOthers.splice(selectedIndex, 1);
    		vm.others[listedIndex].Action = false;
    	}else if(pagename == 'babypage'){
    		selectedIndex = hasId(item, vm.selectedBabyCare);
    		listedIndex = hasId(item, vm.babyCare);
    		vm.selectedBabyCare.splice(selectedIndex, 1);
    		vm.babyCare[listedIndex].Action = false;
    	}
    }

   	function listItemUpdate(item, pagename){

      if(item.mrp_price != "" && item.sale_price != ""){
     		var temp = item;
     		if(pagename == 'healthcare') {
     			if(temp.Action){
     				temp.edit = true;
     				vm.selectedHealthCare.push(temp);
     			} else {
     				var index = hasId(temp, vm.selectedHealthCare);
     				vm.selectedHealthCare.splice(index, 1);
     			}
     		} else if(pagename == 'babypage') {
     			if(temp.Action){
     				temp.edit = true;
     				vm.selectedBabyCare.push(temp);
     			} else {
     				var index = hasId(temp, vm.selectedBabyCare);
     				vm.selectedBabyCare.splice(index, 1);
     			}
     		} else if(pagename =='others') {
     			if(temp.Action){
     				temp.edit = true;
     				vm.selectedOthers.push(temp);
     			} else {
     				var index = hasId(temp, vm.selectedOthers);
     				vm.selectedOthers.splice(index, 1);
     			}
     		}
      } else {
        // console.log('ddd');
        item.Action = false;
        showToast('Please specify sale and MRP price');
      }
   	}

    function showToast(msg){
          $mdToast.show(
            $mdToast.simple()
              .textContent(msg)
              .hideDelay(3000)
          );
    }

   	function hasId(item, list){
   		// console.log(list);
   		for (var i = 0; i < list.length; i++) {
   		  var ele = list[i];
   		  if (ele.otcproduct == item.otcproduct){
   		    return i;
   		   }
   		}

   		return -1;
   	}

    function saveToLocal(ev){
      var otcDetails = vm.selectedBabyCare.concat(vm.selectedHealthCare).concat(vm.selectedOthers);
      console.log(otcDetails);
      $localStorage['otcDetails'] = otcDetails;

      var data = {
        "otccount": 0,
        "pharmacyotc": otcDetails,
        "pharmacy": $localStorage['Pharmausr'].userid
      }

      otcService.postOtcData(data)
        .then(function (res){
          console.log(res);
          showDialog('slected Otc has been saved', ev);
        })


    }

    function showDialog(str, ev){
          $mdDialog.show(
        $mdDialog.alert()
          .clickOutsideToClose(true)
          .title('ThankYou')
          .textContent(str)
          .ok('OK')
          // You can specify either sting with query selector
          .openFrom('#left')
          // or an element
          .closeTo(angular.element(document.querySelector('#right')))
      ).then(function (){
        // $state.go('Home');
      });
          ev.preventDefault();
    }

    babyListener = $scope.$watch(function () {
        return vm.selectedBabyCare.length;
      }, function (newValue) {
      if (angular.isDefined(newValue) && newValue === 0) {
            vm.noBaby = true;
      } else {
        vm.noBaby = false;
      }
    });	

    healthListener = $scope.$watch(function () {
        return vm.selectedHealthCare.length;
      }, function (newValue) {
      if (angular.isDefined(newValue) && newValue === 0) {
            vm.noHealth = true;
      } else {
        vm.noHealth = false;
      }
    }); 

    otherListener = $scope.$watch(function () {
        return vm.selectedOthers.length;
      }, function (newValue) {
      if (angular.isDefined(newValue) && newValue === 0) {
            vm.noOther = true;
      } else {
        vm.noOther = false;
      }
    }); 
  }
})(angular);
(function(angular) {
  'use strict';

  angular
    .module('medszretailer')
    .controller('OrderDetailsController', OrderDetailsController);

  /** @ngInject */
  function OrderDetailsController($timeout, $window, $mdDialog, $document, $state) {
    var vm = this;
    vm.showImgDialog = showImgDialog;
    vm.routeToHome = routeToHome;
    vm.modelVal1 = 31.98;
    vm.modelVal2 = 31.98;
    vm.modelVal3 = 31.98;

    vm.modelQty1 = 2;
    vm.modelQty2 = 2;
    vm.modelQty3 = 2;
    vm.prescTotal = 0;
    vm.otcTotal = 253.50;
    vm.grandTotal = 0;

    vm.calcTotal = calcTotal;
    vm.updateAmt = updateAmt;

    vm.calcTotal();

    function showImgDialog(ev){
    	$mdDialog.show({
          controller: DialogController,
          controllerAs: 'ctrl',
          templateUrl: 'app/orderDetails/orderDetailsDialog.html',
          parent: angular.element($document.body),
          targetEvent: ev,
          clickOutsideToClose: true,
          hasBackdrop: false,
          onRemoving: function () {
            vm.fabIsOpen = false;
          },
          fullscreen: true,
          // locals: {
          //     items: data,
          //     index: index,
          //     title: title,
          //     images: true,
          //     type: listingType
          //   }
        });
    }

    function updateAmt() {
      calcTotal();
    }

    function calcTotal() {
      var row1 = vm.modelQty1 * vm.modelVal1;
      var row2 = vm.modelQty2 * vm.modelVal2;
      var row3 = vm.modelQty3 * vm.modelVal3;
      vm.prescTotal = row1 + row2 + row3;
      vm.grandTotal = vm.otcTotal + vm.prescTotal;
    }

    function DialogController($rootScope, $scope, $log, $mdDialog, $sce) {
      var cs = this;

      cs.cancel = function (){
      	$mdDialog.hide();
      };    

    }

    function routeToHome(ev){
        // console.log('dscsZC');
        $mdDialog.show(
      $mdDialog.alert()
        .clickOutsideToClose(true)
        .title('ThankYou')
        .textContent('Order has been updated')
        .ariaLabel('Left to right demo')
        .ok('OK')
        // You can specify either sting with query selector
        .openFrom('#left')
        // or an element
        .closeTo(angular.element(document.querySelector('#right')))
    ).then(function (){
      $state.go('Home');
    });
        ev.preventDefault();
        
      }

  }
})(angular);
(function(angular) {
  'use strict';

  angular
    .module('medszretailer')
    .controller('OrderController', OrderController);

  /** @ngInject */
  function OrderController($timeout, $window, $state, $localStorage, $http) {
    var vm = this;
    vm.orderSummary = orderSummary;
    vm.orderData = [];
    
    function fillData() {
      $http.get('http://localhost:8080/api/v1/customer/order/', {
          headers: {
              "Authorization": 'JWT ' + $localStorage.accessTok
          }
        }).success(function(response){
          console.log(response)
          vm.orderData = modify(response.results);
        }).error(function(err) {
          // $state.go('Login');
        });
      }

    function modify(arr){
      for(var i=0; i< arr.length; i++){
        switch(arr[i].details) {
          case 'order is in progress':
            arr[i].details = 'In-Progress';
            arr[i].statColor = 'blue-btn';
            break;
          case 'order delivered':
            arr[i].details = 'Received';
            arr[i].statColor = 'primary-btn';
            break;
          case 'order cancelled':
            arr[i].details = 'Cancelled';
            arr[i].statColor = 'red-btn';
            break;
        }
      }
      return arr;
    }

    function orderSummary() {
    	$state.go('OrderDetails');
    }
  }
})(angular);
