(function(angular) {
  'use strict';

  /**
   * @ngdoc service
   * @name medArun.getData
   * @description
   * # getData
   * Factory in the medArun.
   */
  angular.module('medszretailer')
    .factory('getData', getData);

  /** @ngInject */
  function getData($http, $log, $localStorage) {
    var myService = {
      async: function (url) {
        // url = 'json/sample.json';
        var config = {headers:  {
              'Content-Type' : 'application/json',    
              'Authorization': 'JWT ' + angular.isUndefined($localStorage['Pharmausr']) ? '' : $localStorage['Pharmausr'].token
          }
        };
        var promise = $http.get(url, config).then(function (response) {
          return response.data;
        });

        return promise;
      },

      relQuery: function (url, sourceQuery) {
        var query = angular.copy(sourceQuery);
        console.log(sourceQuery);
        // removeEmptyParams(query);
        $log.debug(angular.toJson(query));
        var promise = $http({
          method: 'POST',
          url: url,
          headers: {
            'Content-Type': 'application/json',
            'Authorization': 'JWT ' + $localStorage['Pharmausr'].token 
          },
          data: sourceQuery
        }).then(function (response) {
          var output = angular.fromJson(angular.fromJson(response));
          // output.results.offset = query.offset;
          return output;
        });

        return promise;
      }
    };

    return myService;

    /**
     * Recursively removes empty parameters (objects||arrays) from container object
     * @param  {Object} obj Container object
     */
    function removeEmptyParams(obj) {
      var i;
      if (angular.isObject(obj)) {
        for (i in obj) {
          // because Object.keys(new Date()).length === 0;
          // we have to do some additional check
          if (Object.keys(obj[i]).length === 0 && (obj[i].constructor === Object || obj[i].constructor === Array)) {
            delete obj[i];
          } else {
            removeEmptyParams(obj[i]);
          }
        }
      }
    }
  }
})(angular);

(function(angular) {
  'use strict';

  angular
    .module('medszretailer')
    .controller('blogController', blogController);

  /** @ngInject */
  function blogController($rootScope, $mdDialog, $document, getData) {
    $rootScope.showSideNav = false;
    var vm = this;
    vm.caindex = 0;
    vm.blogData = [];
    vm.popimg = popimg;
    vm.ping_test = function (){
      console.log('ping_test');
    };
    vm.slides = [
      {
        'img': 'assets/img/p1.jpg',
        'name': 'p1'
      },
      {
        'img': 'assets/img/p2.jpg',
        'name': 'p2'
      },
      {
        'img': 'assets/img/p3.jpg',
        'name': 'p3'
      },
      {
        'img': 'assets/img/p4.jpg',
        'name': 'p4'
      }
    ];

    init();
    function init() {
      getData.async('assets/json/blog.json')
        .then(function(res){
          console.log(res);
          vm.blogData = res.results.data;
        }, function(err) {
          console.log(err);
        });
    }

    function popimg(ev, data){
      // console.log(data);
      var arr = [];
      for(var i=0; i<5; i++){
        // console.log('dd');
        arr.push('assets/blog/' + data.date + '/i' + (i+1) + '.jpg');
      }
      $mdDialog.show({
          controller: BlogGallController,
          controllerAs: 'ctrl',
          templateUrl: 'app/blog/bloggallDialog.html',
          parent: angular.element($document.body),
          targetEvent: ev,
          clickOutsideToClose: true,
          hasBackdrop: false,
          // onRemoving: function () {
          //   vm.fabIsOpen = false;
          // },
          fullscreen: true,
          locals: {
              items: arr,
              title: data.place + ' ' + data.objective
            }
        });
    }

    function BlogGallController(locals) {
      var cs = this;
      cs.newItems = [];
      cs.newItems = locals.items;
      cs.title = locals.title;
      // console.log(cs.newItems);

      cs.cancel = function () {
        $mdDialog.hide();
      };
    }
  }
})(angular);

