(function(angular) {
  'use strict';

  angular
    .module('medszretailer')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider, $locationProvider) {
    $stateProvider
      .state('Home', {
        url: '/home',
        templateUrl: 'app/home/home.html',
        controller: 'HomeController',
        controllerAs: 'home'
      })
      .state('Login',{
        url: '/login',
        templateUrl: 'app/login/login.html',
        controller: 'loginController',
        controllerAs: 'login'
      })
      .state('Order',{
        url: '/order',
        templateUrl: 'app/order/order.html',
        controller: 'OrderController',
        controllerAs: 'order'
      })
      .state('OrderDetails',{
        url: '/orderdetails',
        templateUrl: 'app/orderDetails/orderDetails.html',
        controller: 'OrderDetailsController',
        controllerAs: 'od'
      })
      .state('Otc',{
        url: '/otc',
        templateUrl: 'app/otcPage/otc.html',
        controller: 'OtcController',
        controllerAs: 'otc'
      })
      .state('History',{
        url: '/history',
        templateUrl: 'app/history/history.html',
        controller: 'historyController',
        controllerAs: 'history'
      })
      .state('Summary',{
        url: '/summary',
        templateUrl: 'app/summary/summary.html',
        controller: 'summaryController',
        controllerAs: 'summary'
      })
      .state('loyalCust',{
        url: '/loyalcust',
        templateUrl: 'app/loyalcust/loyalcust.html',
        controller: 'loyalcustController',
        controllerAs: 'loyalcust'
      })
      .state('Blog',{
        url: '/blog',
        templateUrl: 'app/blog/blog.html',
        controller: 'blogController',
        controllerAs: 'blog'
      });

    $urlRouterProvider.otherwise('/login');
    $locationProvider.html5Mode(true);
  }

})(angular);

